import { BreadcrumbTypes, EventTypes, StatusCode } from '@monitor/common';

export type voidFun = (...args: any[]) => void;

/**
 * http
 */
export interface HttpData {
  type?: string;
  method?: string;
  time: number;
  url: string; //
  elapsedTime: number; //
  message: string; //
  Status?: number; //
  status?: string; //
  requestData?: {
    httpType: string; //xhr fetch
    method: string; //
    data: any;
  };
  response?: {
    Status: number; //
    data?: any;
  };
}

export interface SdkBase {
  transportData: any; //
  breadcrumb: any; //
  options: any; //
  notify: any; //
}

export abstract class BasePlugin {
  public type: string; //
  constructor(type: string) {
    this.type = type;
  }
  abstract bindOptions(options: object): void; //
  abstract core(sdkBBase: SdkBase): void; //
  abstract transform(data: any): void;
}

export interface BreadcrumbData {
  type: EventTypes; //
  category: BreadcrumbTypes; //
  status: StatusCode; //
  time: number; //
  data: any;
}

/**
 * 
 */
export interface ResourceError {
  time: number;
  message: string; //
  name: string; //js
}
/**
 * 
 */
export interface LongTask {
  time: number;
  name: string; //LongTask
  longTask: any; //
}

/**
 * 
 */
export interface PerformanceData {
  name: string; //FCP
  value: number; //
  rating: string; //
}

/**
 * 
 */
export interface MemoryData {
  name: string; //memory
  memory: {
    jsHeapSizeLimit: number;
    totalJsHeapSize: number;
    usedJsHeapSize: number;
  };
}

/**
 * 
 */
export interface CodeError {
  column: number;
  line: number;
  message: string;
  fileName: string; //
}

export interface RecordScreen {
  recordScreenId: string; //id
  events: string; //
}

// TypeScript interface
/**
 * 
 */
export interface ReportData
  extends HttpData,
    ResourceError,
    LongTask,
    PerformanceData,
    MemoryData,
    CodeError,
    RecordScreen {
  type: string; //
  pageUrl: string; //
  time: number; //
  uuid: string; //
  apiKey: string; //id
  status: string; //
  sdkVersion: string; //
  breadcrumb?: BreadcrumbData[]; //

  //
  deviceInfo: {
    browserVersion: string | number; //
    browser: string; //Chrome
    osVersion: string | number; //
    os: string; //
    ua: string; //
    device: string; //
    device_type: string; //pc
  };
}

export interface Monitor {
  hasError: false; //
  events: string[]; //
  recordScreenId: string; //id
  _loopTimer: number; //timer
  transportData: any; //
  options: any; //
  replaceFlag: {
    //
    [key: string]: any;
  };
  deviceInfo: {
    //
    [key: string]: any;
  };
}

export interface Callback {
  (...args: any[]): any;
}

export interface ReplaceHandler {
  type: EventTypes;
  callback: Callback;
}

export type ReplaceCallback = (data: any) => void;

export interface IAnyObject {
  [key: string]: any;
}

export interface ErrorTarget {
  target: {
    localName?: string;
  };
  error?: any;
  message?: string;
}

export interface ResourceTarget {
  src?: string;
  href?: string;
  localName?: string;
}
/**
 *
 **/
export interface ResourceError {
  time: number;
  message: string; //
  name: string; //:js
}

export interface RouteHistory {
  from: string;
  to: string;
}
